import React, { lazy, Suspense } from 'react';
import { Switch, Route } from "react-router-dom";

const Step = lazy(() => import('remote/step'))
const NextjsRemotePage = lazy(() => import('remote/nextjs-remote-page'))

function Home() {
  return "Home"
}

function About() {
  return "about"
}


function App() {
  return (
   <>
    <Switch>
      <Route exact path="/about">
        <About />
      </Route>
      <Route path="/step">
        <Suspense fallback={<>...</>}>
          <Step/>
        </Suspense>
      </Route>
      <Route path="/">
        <Home />
      </Route>
    </Switch>
   </> 
  )
}

export default App;
